import React, { Component } from 'react';



export class ToDo extends Component {
     
    constructor(props){
        super(props);

        this.state = {
            lists : [],
            currentText : ""
        };
    }


    currentText = e => this.setState({currentText: e.target.value});

    addlist = (e) =>{
        e.preventDefault();

       let {currentText, lists} = this.state;

       lists.push(currentText);
       this.setState({lists: lists});

    }

    remove = (k) =>{
        let {lists} = this.state;

        lists.splice(k, 1);
        this.setState({lists: lists});

    }


    render() {
        return (
            <div className="task-contain">
                <form>
                    <div className="grpme">
                    <input type="text"  name="text" placeholder="Type Here" onChange={this.currentText}/>
                    <button className="btn-sub" onClick={this.addlist}>Add</button>
                    </div>
                </form>

                <ul className="listMe" >
                    {this.state.lists.map((val, key)=>(<li><span>{val}</span><button className="btn-rem" onClick={()=>{this.remove(key)}}>Remove</button></li>)
                    )} 
                </ul>
            </div>
        );

    }
}

export default ToDo;
